import React, { Component } from 'react';
import { Link } from 'react-router';
import { connect } from 'react-redux';
import { getArtPiece, submitPurchaseBidForm, closeModal } from '../../actions'
import { _amp } from '../../index';

import Preloader from '../_common/Preloader';
import PostSuccess from './PostSuccess';
import EmailCollection from '../_common/EmailCollection';
import ImgRow from './ImgRow';

class ArtPiece extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showPurchaseForm: true,
            showPurchaseFormBtn: true,
            bid: null,
            name: '',
            email: '',
            phone: 0
        }
    }

    componentWillMount() {
        if(this.props.params === undefined || this.props.params.id === undefined || this.props.params.pieceId === undefined) {
            // console.log('ArtPiece in modal')
            // console.log(this.props.artPiece)
            _amp.logEvent('pageView-piece', { pieceId: this.props.artPiece.pieceId, artistUsername: this.props.artPiece.artistUsername });
        } else {
            // console.log(this.props.params)
            _amp.logEvent('pageView-piece', { pieceId: this.props.params.pieceId, artistUsername: this.props.params.id });
            this.props.getArtPiece(this.props.params.id, this.props.params.pieceId);        
        }
    }

    submitBid() {
        // console.log('this.props.pieceInfo : ', this.props.pieceInfo);
        // console.log('this.props.artPiece : ', this.props.artPiece);
        this.setState({ showPurchaseFormBtn: false })

        let comparator;
        comparator = (this.props.pieceInfo !== undefined && this.props.pieceInfo !== false ? this.props.pieceInfo.minPrice : this.props.artPiece.minPrice)
        // console.log(comparator);

        if(this.state.bid > comparator) {
            if(this.state.name === '') {
                this.setState({ showPurchaseFormBtn: true })
                alert('That isn\'t a valid name.');
                return false;
            }
            if(!this.validateEmail(this.state.email)) {
                this.setState({ showPurchaseFormBtn: true })
                alert('This isn\'t a valid email address');
                return false;
            }
            if(this.state.phone.toString().length !== 10) {
                this.setState({ showPurchaseFormBtn: true })
                alert('This isn\'t a valid phone number.');
                return false;
            }
            let submissionObj = {
                formSource: 'webapp',
                artistUsername: (this.props.params ? this.props.params.id : this.props.artPiece.artistUsername),
                email: this.state.email,
                name: this.state.name,
                phone: this.state.phone,
                bid: this.state.bid,
                pieceId: (this.props.params ? this.props.params.pieceId : this.props.artPiece.pieceId)
            }
            _amp.logEvent('pageView-purchaseFormSubmission', { pieceId: (this.props.params ? this.props.params.pieceId : this.props.artPiece.pieceId), artistUsername: (this.props.params ? this.props.params.id : this.props.artPiece.artistUsername), userEmail: this.state.email });
            // console.log('sub obj ', submissionObj);
            this.props.submitPurchaseBidForm(submissionObj);
            return;
        } else {
            alert("The bid must be at least $" + comparator)
        }
    }

    submitPrice() {
        this.setState({ showPurchaseFormBtn: false })
        if(this.state.name === '') {
            this.setState({ showPurchaseFormBtn: true })
            alert('That isn\'t a valid name.');
            return false;
        }
        if(!this.validateEmail(this.state.email)) {
            this.setState({ showPurchaseFormBtn: true })
            alert('This isn\'t a valid email address');
            return false;
        }
        if(this.state.phone.toString().length !== 10) {
            this.setState({ showPurchaseFormBtn: true })
            alert('This isn\'t a valid phone number.');
            return false;
        }

        let submissionObj = {
            formSource: 'webapp',
            artistUsername: (this.props.params ? this.props.params.id : this.props.artPiece.artistUsername),
            email: this.state.email,
            name: this.state.name,
            phone: this.state.phone,
            price: (this.props.params ? this.props.pieceInfo.price : this.props.artPiece.price),
            pieceId: (this.props.params ? this.props.params.pieceId : this.props.artPiece.pieceId)
        }

        _amp.logEvent('pageView-purchaseFormSubmission', {
            pieceId: (this.props.params ? this.props.params.pieceId : this.props.artPiece.pieceId),
            artistUsername: (this.props.params ? this.props.params.id : this.props.artPiece.artistUsername), 
            userEmail: this.state.email
        });
        this.props.submitPurchaseBidForm(submissionObj);
    }

    validateEmail(email) {
        // eslint-disable-next-line
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }

    togglePurchaseForm() {
        // console.log(this.props.params || this.props.artPiece)
        _amp.logEvent('pageView-piecePurchaseForm', {
            pieceId: (this.props.params !== undefined ? this.props.params.pieceId : this.props.artPiece.pieceId),
            artistUsername: (this.props.params !== undefined ? this.props.params.id : this.props.artPiece.artistUsername) 
        });
        this.setState({ showPurchaseForm: false })
    }

    closeModal() {
        console.log('close modal')
        this.props.closeModal('pieceModal');
    }

    render() {
        if (this.props.postedForm) {
            return (
                <div>
                    <PostSuccess submissionEmail={this.state.email} />
                    <EmailCollection />
                </div>
            )
        } else if(this.props.artPiece || this.props.pieceInfo) {
            let pI = this.props.artPiece || this.props.pieceInfo ;
            // console.log(pI);
            return (
                <div className="container" id="pgArtPiece">

                    {   this.props.showCloseModalBtn ?
                        <button
                            className="btn closeBtnTop"
                            onClick={() => this.closeModal()}
                            >X</button>
                        : null }

                    {/* FIRST ROW || Names, Social, */}
                    <ImgRow imgUrls={pI.imgUrls} />

                    {/* SECOND ROW || Bid form */}
                    <div className="row purchaseRow">
                        <div className="col-sm-4 col-sm-offset-2 leftCol">
                            <h3>{pI.pieceName}</h3>
                            <p>{pI.pieceDescription}</p>
                            <h5>Artist: {pI.artistUsername}</h5>
                            <Link to={'/artist/' + pI.artistUsername}>
                                <button onClick={() => this.closeModal()} className="btn">See Artist's Works</button>
                            </Link>
                        </div>

                        <div className="col-sm-4 rightCol">
                            {
                                pI.isBid ? 
                                    <div className="purchase bid">
                                        <p className="suggested_bid">Suggested Bid: ${pI.suggestedPrice}</p>
                                        <p className="min_bid">Min Bid: ${pI.minPrice}</p>
                                        {
                                            this.state.showPurchaseForm ?
                                                <button onClick={() => this.togglePurchaseForm()} className="btn btn-info bidPurchaseBtn">Place a Bid</button>
                                            :
                                                <div className="bidForm">
                                                    <hr/>
                                                    <h4>Bid on Piece</h4>
                                                    <label htmlFor="">Bid Amount (must be more than ${pI.minPrice})</label>
                                                    <input type="number" onChange={event => this.setState({bid: event.target.value})}/>
                                                    <label htmlFor="">Name</label>
                                                    <input type="text" onChange={event => this.setState({name: event.target.value})}/>
                                                    <label htmlFor="">Email</label>
                                                    <input type="text" onChange={event => this.setState({email: event.target.value})}/>
                                                    <label htmlFor="">Phone</label>
                                                    <input type="number" onChange={event => this.setState({phone: event.target.value})}/>
                                                    {
                                                        this.state.showPurchaseFormBtn 
                                                        ?
                                                        <button className="btn btn-success bidPurchaseSubmit" onClick={() => this.submitBid()}>Submit</button>
                                                        :
                                                        <Preloader />
                                                    }
                                                </div>
                                        }
                                    </div>
                                :
                                    <div className="purchase price">
                                        <p>Price: ${pI.price}</p>
                                        {
                                            this.state.showPurchaseForm ?
                                                <button onClick={() => this.togglePurchaseForm()} className="btn btn-info bidPurchaseBtn">Buy Piece</button>
                                            :
                                                <div className="purchaseForm">
                                                    <hr/>
                                                    <h4>Purchase Form</h4>
                                                    <label htmlFor="">Name</label>
                                                    <input type="text" onChange={event => this.setState({name: event.target.value})}/>
                                                    <label htmlFor="">Email</label>
                                                    <input type="text" onChange={event => this.setState({email: event.target.value})}/>
                                                    <label htmlFor="">Phone</label>
                                                    <input type="number" onChange={event => this.setState({phone: event.target.value})}/>
                                                    {
                                                        this.state.showPurchaseFormBtn 
                                                        ?
                                                        <button className="btn btn-success bidPurchaseSubmit" onClick={() => this.submitPrice()}>Submit</button>
                                                        :
                                                        <Preloader />
                                                    }
                                                </div>
                                        }
                                    </div>
                            }
                            
                        </div>
                    </div>

                    {   this.props.showCloseModalBtn ?
                            <button
                                className="btn closeBtnBtm"
                                onClick={() => this.closeModal()}
                                >Close</button>
                            : null }

                    <EmailCollection />
                </div>
            )
        } else {
            return (
                <div id="pgArtPiece">
                    <Preloader />
                </div>
            )
        }
    }
}

const mapStateToProps = state => {
    // console.log(state)

    let pieceInfo = false;
    let postedForm = false;

    if(state.pieceInfo) {
        pieceInfo = state.pieceInfo;
    }
    
    if(state.purchaseFormPost) {
        postedForm = true;
    }

    return { pieceInfo, postedForm }
}

export default connect(mapStateToProps, { getArtPiece, submitPurchaseBidForm, closeModal })(ArtPiece);


