import React, { Component } from 'react';

class PieceComponent extends Component {

    render() {
        let pieceObj = this.props.pieceObj;
        // console.log(pieceObj)
        let pieceImg = pieceObj.imgUrls[Object.keys(pieceObj.imgUrls)[0]];


        return (
            <div className="col-sm-4 pieceObj">
                <img src={pieceImg} alt=""/>
                <p></p>
            </div>
        )
    }
}


export default PieceComponent;


