import { GET_FEATURED_PIECES, GET_ARTISTS, SUBSCRIBE_USER, GET_ARTIST_INFO, GET_PIECE_INFO, POST_PURCHASE_FORM, PULL_FEED, ARTIST_SIGNUP_EMAIL, OPEN_PIECE_MODAL, CLOSE_PIECE_MODAL, SUBMIT_JOB_FORM } from '../constants';
import axios from 'axios';
import { daBaseUrl } from '../index';

export const getFeaturedPieces = () => {
    const action = {
        type: GET_FEATURED_PIECES,
        pieces: 'Yay'
    }
    return action;
}

export const subscribeEmail = (userEmail) => {
    return (dispatch) => {
        return axios.get(daBaseUrl + '/newEmailSub?email=' + userEmail).then((response) => {
            // console.log(response.status);
            dispatch({
                type: SUBSCRIBE_USER,
                response: response.data
            })
        })
    }
}

export const artistEmailSignup = (artistEmail) => {
    return (dispatch) => {
        return axios.get(daBaseUrl + '/newArtistEmail?email=' + artistEmail).then((response) => {
            dispatch({
                type: ARTIST_SIGNUP_EMAIL,
                response: response.data
            })
        })
    }
}

export const getArtists = () => {

    return (dispatch) => {
        return axios.get(daBaseUrl + '/getAllArtists').then((response) => {
            dispatch({
                type: GET_ARTISTS,
                artists: response.data
            });
        })
    }
}

export const getArtistInfo = (artistUsername) => {
    return (dispatch) => {
        return axios.get(daBaseUrl + '/getArtistInfo?artistUsername=' + artistUsername).then((response) => {
            dispatch({
                type: GET_ARTIST_INFO,
                artistInfo: response.data
            });
        })
    }
}

export const getArtPiece = (artistUsername, pieceId, src) => {
    return (dispatch) => {
        return axios.get(daBaseUrl + '/getPieceInfo3?artistUsername=' + artistUsername + '&pieceId=' + pieceId).then((response) => {
            dispatch({
                type: GET_PIECE_INFO,
                pieceInfo: response.data
            })
        })
    }
}

export const submitPurchaseBidForm = (submissionObj) => {
    return (dispatch) => {
        return axios.post(daBaseUrl + '/submitPurchaseForm', submissionObj).then(response => {
            dispatch({
                type: POST_PURCHASE_FORM,
                postResponse: response.data
            })
        })
    }
}

export const pullFeed = () => {
    return (dispatch) => {
        return axios.get(daBaseUrl + '/feedPull').then(response => {
            dispatch({
                type: PULL_FEED,
                pulledFeed: response.data
            })
        })
    }
}

export const openPieceModal = (piece) => {
    // console.log(piece);
    const action = {
        type: OPEN_PIECE_MODAL,
        piece: piece
    }
    return action;
}

export const closeModal = (modalName) => {
    console.log('Closing: ' + modalName)
    switch(modalName) {
        case 'pieceModal':
            const action = {
                type: CLOSE_PIECE_MODAL
            }
            return action;

        default:
            return {};
    }
}

export const submitJobForm = (formObj) => {
    console.log('actions -> formObj')
    return (dispatch) => {
        return axios.post(daBaseUrl + '/submitJobForm', formObj).then(response => {
            console.log(response);
            dispatch({
                type: SUBMIT_JOB_FORM,
                postResponse: response.data
            })
        })
    }
}



