import React, { Component } from 'react';

class PostSuccess extends Component {

    render() {
        let purchaseMsg = 'You\'ll receieve an email shortly.';

        if(this.props.submissionEmail) {
            purchaseMsg = 'You\'ll receieve an email at ' + this.props.submissionEmail + ' shortly.'
        }

        return (
            <div className="text-center" id="pgPostSuccess">
                <h4>Your purchase request has been submitted!</h4>
                <p>{purchaseMsg}</p>
            </div>
        )
    }
}

export default PostSuccess;


