import React, { Component } from 'react';
import { connect } from 'react-redux';
import { closeModal } from '../../actions';
import Modal from '../_common/Modal';
import ArtPiece from '../ArtPiece/ArtPiece';

class PieceModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            modalName: 'pieceModal'
        }
    }

    closeModal() {
        console.log(this.state.modalName)
        this.props.closeModal(this.state.modalName)
    }

    render() {
        if(this.props.openPiece) {
            let piece = this.props.openPiece
            piece['img1'] = piece.imgUrls[Object.keys(piece.imgUrls)[0]];
            console.log(piece)
            return (
                <div className="mdPieceModal">
                    <Modal modalName={this.state.modalName}>
                        <ArtPiece showCloseModalBtn={true} artPiece={piece} />
                    </Modal>
                </div>
            )
        }
        return (
            null
        )
    }
}

const mapStateToProps = state => {
    return {}
}

export default connect(mapStateToProps, { closeModal })(PieceModal);


