import React, { Component } from 'react';
import { Link } from 'react-router';

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            newNav: true
        }
    }

    closeMenu() {
        document.getElementById('daToggle').checked = !document.getElementById('daToggle').checked;
    }

    render() {
        if(this.state.newNav) {
            return (
                // eslint-disable-next-line
                <nav role="navigation" className="newNav">
                    <div className="whiteBar">
                        <p className="newLogo">a<Link className="removeTextDeco" to={'/'}>muralist</Link></p>
                    </div>
                    <div id="menuToggle">
                        <input id="daToggle" type="checkbox" />
                        
                        <span></span>
                        <span></span>
                        <span></span>
                        
                        <ul id="menu">
                            <Link onClick={() => this.closeMenu()} to={'/'}><li>Home</li></Link>
                            <Link onClick={() => this.closeMenu()} to={'/discover'}><li>Discover</li></Link>
                            <Link onClick={() => this.closeMenu()} to={'/artists'}><li>Artists</li></Link>
                            {/* <Link onClick={() => this.closeMenu()} to={'/join-us'}><li>Join Our Team</li></Link> */}
                            <Link onClick={() => this.closeMenu()} to={'/report-a-bug'}><li className="report">Report A Bug</li></Link>
                        </ul>
                    </div>
                </nav>
            )
        } else {
            return (
                <nav className="navbar-fixed-to daNav navbar">
                    <div className="container">
                        <div className="navbar-header">
                            <span className="navbar-brand">
                                <Link className="removeTextDeco" to={'/'}><p className="newLogo">muralist</p></Link>
                            </span>
                        </div>
                    </div>
                </nav>
            )
        }



    }
}

export default Home;


