import React, { Component } from 'react';
import { connect } from 'react-redux';
import { pullFeed } from '../../actions'
import DiscoverPiece from './DiscoverPiece';
import PieceModal from './PieceModal';
import { _amp } from '../../index';

class Discover extends Component {
    constructor(props) {
        super(props);
        this.state = {
            pagStart: 0,
            pagEnd: 9,
            showMoreBtn: true
        }
    }

    componentWillMount() {
        _amp.logEvent('pageView-discover(feed)');
        this.props.pullFeed();        
    }

    loadMore() {
        console.log(this.props.pulledFeed)
        // this.setState({ pagStart: 10, pagEnd: 19 })

        // IF pageEnd is less than arrLength
        if(this.state.pagEnd < this.props.pulledFeed.arrLength) {
            // IF pageEnd + 10 is less than arrLength
            if((this.state.pagEnd + 10) < this.props.pulledFeed.arrLength) {
                // Then we'll have more pages after this
                this.setState({ pagEnd: (this.state.pagEnd + 10) })
            } else {
                this.setState({ pagEnd: (this.props.pulledFeed.arrLength - 1) , showMoreBtn: false });
            }
        }
    }

    render() {
        if(this.props.pulledFeed) {
            let piecesObj = this.props.pulledFeed.piecesObj;
            let piecesKeys = this.props.pulledFeed.shuffledPiecesKeysArr;
            let pF = [];
            for(let i = this.state.pagStart; i <= this.state.pagEnd;i++) {
                if(piecesObj[piecesKeys[i]]) {
                    let currPiece = piecesObj[piecesKeys[i]];
                    currPiece['pieceId'] = piecesKeys[i];
                    if(currPiece['imgUrls'] !== undefined) {
                        pF.push(currPiece)
                    }
                }
            }
            return(
                <div id="pgDiscover">
                    {
                        pF.map((piece, index) => 
                            <DiscoverPiece key={index} piece={piece} />
                        )
                    }
                    <div className="row buttons">
                        <div className="col-lg-4 col-lg-offset-4 col-md-6 col-md-offset-3 col-sm-10 col-sm-offset-1">
                            {
                                this.state.showMoreBtn ?
                                    <button className="btn loadMore" onClick={() => this.loadMore()}>
                                        Load More
                                        <i className="fa fa-caret-down" aria-hidden="true"></i>
                                    </button>
                                :
                                    <div></div>
                            }
                        </div>
                    </div>
                    <PieceModal openPiece={this.props.pieceModal} />
                </div>
            )
        }

        // console.log(this.props.pulledFeed)
        return (
            <div>
                <div className="lds-roller"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
            </div>
        )
    }
}

const mapStateToProps = state => {

    let pulledFeed = false;
    if(state.pulledFeed) {
        pulledFeed = state.pulledFeed;
    }

    let pieceModal = false;
    if(state.openPiece) {
        pieceModal = state.openPiece;
    }

    return { pulledFeed, pieceModal }
}

export default connect(mapStateToProps, { pullFeed })(Discover);


