import React, { Component } from 'react';

class SocialLinks extends Component {


    render() {
        // console.log(this.props.linksObj)
        let sL = this.props.linksObj;
        return (
            <div className="socialLinks">
                { sL.ig === '' ? null : <a href={sL.ig} target="_blank"><i className="fa fa-instagram"></i></a> }
                { sL.twitter === '' ? null : <a href={sL.twitter} target="_blank"><i className="fa fa-twitter"></i></a> }
                { sL.fb === '' ? null : <a href={sL.fb} target="_blank"><i className="fa fa-facebook"></i></a> }
            </div>
        ) 
    }
}


export default SocialLinks;


