import React, { Component } from 'react';
import { Link } from 'react-router';

import ScrollDetector from './Discover/ScrollDetector';


class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userEmail: ''
        }
    }


    render() {
        return (
            <div className="text-center" id="footerNew">
                <p className="copy">© 2018 All rights reserved. An <a className="footerLink" href="http://everestdigital.ca/" target="_blank" rel="noopener noreferrer">EverestDigital</a> Product.</p>
                <div className="igFa"><a href="https://www.instagram.com/muralist.design/" target="_blank" rel="noopener noreferrer"><i className="fa fa-instagram fa-1x"></i></a></div> 

                <Link to={'/report-a-bug'}><p className="reportBug">Report A Bug</p></Link>
                <ScrollDetector />
            </div>
        )
    }
}

export default Footer;


