import { GET_FEATURED_PIECES,
            GET_ARTISTS,
            SUBSCRIBE_USER,
            GET_ARTIST_INFO,
            GET_PIECE_INFO,
            POST_PURCHASE_FORM, 
            PULL_FEED, 
            ARTIST_SIGNUP_EMAIL, 
            OPEN_PIECE_MODAL, 
            CLOSE_PIECE_MODAL,
            SUBMIT_JOB_FORM
        } from '../constants';

const reducers = (state = {}, action) => {
    let featuredPieces = null;
    // state = {
    //     openPiece: false
    // }

    switch(action.type) {
        case GET_FEATURED_PIECES:
            featuredPieces = [...state, 'piece name'];
            return featuredPieces;

        case GET_ARTISTS:
            return {...state, artists: action.artists};

        case GET_ARTIST_INFO:
            return {...state, artistInfo: action.artistInfo};

        case GET_PIECE_INFO:
            return {...state, pieceInfo: action.pieceInfo};

        case SUBSCRIBE_USER:
            return {...state, subscribeUserSuccess: true}

        case ARTIST_SIGNUP_EMAIL:
            return {...state, postResponse: action.response}

        case POST_PURCHASE_FORM:
            return {...state, purchaseFormPost: action.postResponse}

        case PULL_FEED:
            return {...state, pulledFeed: action.pulledFeed}

        case OPEN_PIECE_MODAL: 
            return {...state, openPiece: action.piece}

        case CLOSE_PIECE_MODAL: 
            if(state.purchaseFormPost) {
                return {...state, purchaseFormPost:false, openPiece: false}
            } else {
                return {...state, openPiece: false}
            }

        case SUBMIT_JOB_FORM: 
            return {...state, jobFormResponse: action.postResponse}

        default:
            return state;
    }
}

export default reducers;

