import React, { Component } from 'react';
import Modal from '../_common/Modal';

import { connect } from 'react-redux';
import { submitJobForm } from '../../actions';

class ApplyModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            phone: '',
            about: ''
        }
    }

    closeModalCaller(){
        this.props.closeModal();
    }

    submitForm() {
        if(this.state.name === '') {
            alert('Name cannot be empty.');
            return;
        }

        if(this.validateEmail(this.state.email) === false) {
            alert('Invalid email.');
            return;
        }

        if(this.validatePhone(this.state.phone) === false) {
            alert('Invalid phone.');
            return;
        }
        
        if(this.state.about === '') {
            alert('About section cannot be empty.');
            return;
        }

        this.props.submitJobForm(this.state);
        alert("Great, we'll be in touch shortly!")
        this.closeModalCaller();
    }

    validatePhone(phone) {
        // eslint-disable-next-line
        var re = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im;
        return re.test(String(phone));
    }

    validateEmail(email) {
        // eslint-disable-next-line
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }

    render() {
        if(this.props.showApplyModal) {
            return (
                <div className="mdApplyModal">
                    <Modal modalName={this.state.modalName}>
                        <button className="button closeBtn" onClick={() => this.closeModalCaller()}>X</button>

                        <div className="col-sm-12 main">
                            <p className="daLabel">Interest in Role</p>
                            <hr/>
                            <p className="jobTitle">Art Curator (Founding Partner)</p>

                            <div className="inputRow">
                                <input type="text" 
                                        value={this.state.name}
                                        onChange={event => this.setState({ name: event.target.value })}
                                        className="name"
                                        placeholder="Your name" />
                                <input type="text"
                                        value={this.state.email}
                                        onChange={event => this.setState({ email: event.target.value })}
                                        className="email"
                                        placeholder="Your email" />
                            </div>

                            <input type="text"
                                    value={this.state.phone}
                                    onChange={event => this.setState({ phone: event.target.value })}
                                    className="phone"
                                    placeholder="Your phone number" />

                            <textarea className="aboutArtist"
                                        value={this.state.about}
                                        onChange={event => this.setState({ about: event.target.value })}
                                        placeholder="Tell us a bit about you, share your portfolio, Instagram or whatever you think shows your skills best!"></textarea>

                            <button className="btn submitBtn" onClick={() => this.submitForm()}>Submit</button>
                        </div>
                    </Modal>
                </div>
            )
        } else {
            return (
                <span></span>
            )
        }

    }
}

const mapStateToProps = state => {
    return {}
}

export default connect(mapStateToProps, { submitJobForm })(ApplyModal);


