import React from 'react';
import ReactDOM from 'react-dom';
import Amplitude from 'react-amplitude';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import { Router, hashHistory, Route, Redirect } from 'react-router';
import { createStore, applyMiddleware } from 'redux';

import App from './components/App';
import Story from './components/Story/Story';
import ArtistsWhySignup from './components/Marketing/ArtistsWhySignup';
import Artists from './components/Artists/Artists';
import ArtistProfile from './components/ArtistProfile/ArtistProfile';
import ArtPiece from './components/ArtPiece/ArtPiece';
import Discover from './components/Discover/Discover';
import JoinUs from './components/JoinUs/JoinUs';
import ReportABug from './components/ReportABug/ReportABug';

import '../node_modules/font-awesome/css/font-awesome.min.css';
import '../node_modules/bootstrap3/dist/css/bootstrap.min.css';
import './index.css';
import reducers from './reducers';
const store = createStore(reducers, applyMiddleware(thunk))


// PROD
const configObj  = { baseUrl: 'https://us-central1-art-auction-2ef27.cloudfunctions.net', amplitudeKey: '61805aa85ceeaae4680ffa861d22b9ca'};
// DEV
// const configObj  = { baseUrl: 'https://us-central1-muralist-dev.cloudfunctions.net', amplitudeKey: '5e09ac32b11544d51dece40b53c6b27d' }

export const daBaseUrl = configObj.baseUrl;
Amplitude.init(configObj.amplitudeKey);
export const _amp = Amplitude;

document.addEventListener('DOMContentLoaded', function() {
    ReactDOM.render(
        <Provider store={store}>
            <Router history={hashHistory}>
                <Redirect from="/" to="story" />
                <Route path="/" component={App}>
                    <Route path="story" component={Story} /> 
                    <Route path="artists-why-signup" component={ArtistsWhySignup} /> 
                    <Route path="artists" component={Artists} /> 
                    <Route path="artist/:id" component={ArtistProfile} /> 
                    <Route path="artist/:id/:pieceId" component={ArtPiece} /> 
                    <Route path="discover" component={Discover} /> 
                    <Route path="join-us" component={JoinUs} /> 
                    <Route path="report-a-bug" component={ReportABug} /> 
                </Route>
            </Router>
        </Provider>,
        document.getElementById('root')
    )
});


