import React, { Component } from 'react';
import { connect } from 'react-redux';
import { closeModal } from '../../actions';

class Modal extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    closeModal() {
        if(this.props.modalName === 'pieceModal') {
            this.props.closeModal(this.props.modalName);
        } else {
            console.log('Not gonna close it');
        }
    }

    render() {
        return (
            <div id="modal">
                <div className="modal-bg" onClick={() => {this.closeModal()}}></div>
                <div className="daModal" >
                    {this.props.children}
                </div>
            </div>
        )
    }
}

export default connect(null, { closeModal })(Modal);


