import React, { Component } from 'react';

import PieceRow from './PieceRow';

class ArtPieces extends Component {

    render() {
        let piecesArr = getPiecesArr(this.props.pieces);
        // console.log(piecesArr);

        if(piecesArr.length > 0) {

            return (
                <div className="">
                    {
                        piecesArr.map((piecesRowArr, index) => 
                            <PieceRow 
                                piecesRowArr={piecesRowArr} 
                                artistUsername={this.props.artistUsername} 
                                key={index} 
                            />
                        )
                    }
                </div>
            )
        } else {
            return (
                <div></div>
            )
        }

    }
}

const getPiecesArr = (piecesRaw) => {
    // console.log(piecesRaw)
    let piecesKeys = Object.keys(piecesRaw)
    let returnArr = []

    if(piecesKeys.length > 0) {
        for(var i = 0; i < piecesKeys.length; i+=3) {
            // console.log( piecesKeys[i] );
            // console.log( piecesRaw[ piecesKeys[i] ] )
            let temp = [];

            if(piecesRaw[piecesKeys[i]]) {
                let imgObj = piecesRaw[piecesKeys[i]]
                imgObj['imgId'] = piecesKeys[i];
                temp.push(imgObj)
            }
            if(piecesRaw[piecesKeys[i+1]]) {
                let imgObj = piecesRaw[piecesKeys[i+1]]
                imgObj['imgId'] = piecesKeys[i+1];
                temp.push(imgObj)
            }
            if(piecesRaw[piecesKeys[i+2]]) {
                let imgObj = piecesRaw[piecesKeys[i+2]]
                imgObj['imgId'] = piecesKeys[i+2];
                temp.push(imgObj)
            }
            // let temp = [ piecesKeys[i], piecesKeys[i+1], piecesKeys[i+2] ];

            returnArr.push(temp)
        }
    }
    return returnArr;
}

export default ArtPieces;


