import React, { Component } from 'react';
import { connect } from 'react-redux';
import { openPieceModal } from '../../actions'
// import { Link } from 'react-router';

class DiscoverPiece extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    openPieceModal() {
        // console.log(this.props.piece)
        this.props.openPieceModal(this.props.piece);
    }

    render() {
        // console.log(this.props.piece)
        let { piece } = this.props;
        // console.log(piece)
        let pieceImg = piece.imgUrls[Object.keys(piece.imgUrls)[0]]
        return (
            <div className="row discoverPiece">
                <div 
                    className="daPiece col-md-6 col-md-offset-3 col-sm-10 col-sm-offset-1"
                    onClick={() => this.openPieceModal()}
                >
                    {/* <Link to={'/artist/' + piece.artistUsername + '/' + piece.pieceId}> */}
                        <img src={pieceImg} alt=""/>

                        {/* <p className="price">${piece.price}</p> */}
                        <div className="row">
                            <div className="col-sm-8 colLeft">
                                {/* <p className="small">Piece Name</p> */}
                                <p className="big">{piece.pieceName} <span className="mid">by {piece.artistUsername}</span></p>
                                <p className="price">${piece.price}</p>
                            </div>
                            <div className="col-sm-4 colRight">
                                {/* <p className="small">Artist Username</p> */}
                                <button className="btn moreInfoBtn">More info</button>
                            </div>
                        </div>
                    {/* </Link> */}
                </div>
            </div>
        )
    }
}

const mapStateTopProps = state => {
    return {}
}


export default connect(mapStateTopProps, { openPieceModal })(DiscoverPiece);


