import React, { Component } from 'react';

class ImgRow extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        // console.log(this.props);
        let mainImg = this.props.imgUrls[Object.keys(this.props.imgUrls)[0]]
        return (
            <div className="row imgRow">
                <div className="text-center column col-sm-6 col-sm-offset-1 leftCol">
                    <img className="img1" src={mainImg} alt=""/>
                </div>
                <div className="text-center column col-sm-4 rightCol">
                    <div className="img1">
                        <img src={mainImg} alt=""/>
                    </div>
                    <div className="img2">
                        <img src={mainImg} alt=""/>
                    </div>
                </div>
            </div>
        )
    }
}

export default ImgRow;


