import React, { Component } from 'react';

class ReportABug extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        return (
            <div id="pgReportABug" className="row">
                <div className="col-sm-8 col-sm-offset-2">
                    <p className="line1">Having trouble with muralist?</p>
                    <p className="line2">Fill out the form below and we'll be in touch shortly!</p>
                    <div className="row">
                        <div className="col-sm-6">
                            <input type="text"
                                placeholder="Name"
                            />
                        </div>
                        <div className="col-sm-6">
                            <input type="text"
                                placeholder="Email"
                            />
                        </div>
                    </div>
                    <textarea name="" placeholder="Tell us what the issue is... (Which page was it on? What weren't you able to do?)" id="" ></textarea>
                    <br/>
                    <button className="btn">Submit</button>

                </div>
            </div>
        )
    }
}

export default ReportABug;


