import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getArtists } from '../../actions';
import ArtistProfileSmall from './ArtistProfileSmall';

class Artists extends Component {
    constructor(props) {
        super(props);
        this.state = {
            artists: []
        }
    }

    componentDidMount() {
        this.props.getArtists(); 
    }

    render() {

        return (
            <div id="pgArtists">
                <div className="text-center jumbotron jumbotron-fluid">
                    <div className="container">
                        <h2>The Artists</h2>
                        <h4>Toronto</h4>
                    </div>
                </div>

                <div className="container">
                    { (this.props.artistsArr.length > 0) ? <span></span> : <div className="lds-roller"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>}
                </div>
                <div className="container">
                    {
                        this.props.artistsArr.map((artist, index) => 
                            <ArtistProfileSmall key={index} artistObj={artist} />
                        )
                    }
                </div>

            </div>
        )
    }
}

const mapStateToProps = state => {

    // console.log(state, 'all view')
    let artistsArr = [];

    // const data = state.data;
    // var userData = data.userData;
    // const user = state.auth.user;

    if(state.artists){
        // console.log(state.artists)
        for(let key in state.artists) {
            // console.log(key);
            let artistObj = state.artists[key];
            artistObj.key = key;
            if(artistObj.artPieces !== undefined) {
                artistsArr.push(artistObj)
            }
        }

        // userData = _.map(userData, (val) => {
        //     return { ...val };
        // });
    }
    return { artistsArr };
};

export default connect(mapStateToProps, { getArtists } )(Artists);


