import React, { Component } from 'react';
import { connect } from 'react-redux';
import { subscribeEmail } from '../../actions';
import { _amp } from '../../index';
// import { _amp } from '../../index';

class EmailCollection extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userEmail: ''
        }
    }

    subEmail() {
        if(this.validateEmail(this.state.userEmail)) {
            _amp.logEvent('Email capture', {userEmail: this.state.userEmail});
            this.props.subscribeEmail(this.state.userEmail);
            alert('Great! You\'ll be the first to know when we launch.')
            this.setState({ userEmail: ''})
        } else {
            alert('Not a valid email address.')
        }
    }

    validateEmail(email) {
        // eslint-disable-next-line
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }

    render() {
        return (
            <div className="container" id="emailCollection">
                <div className="col-sm-6 col-sm-offset-2 leftCol">
                    <p className="line1">Signup for updates!</p>
                    <p className="line2">The stable beta is launching shortly.</p>
                </div>
                <div className="col-sm-2 rightCol">
                    <input
                        type="text"
                        placeholder="Email"
                        className="form-control"
                        value={this.state.userEmail}
                        onChange={event => this.setState({ userEmail: event.target.value })}
                    />

                    <button
                        className="btn"
                        type="button"
                        onClick={() => this.subEmail()}
                    >
                        Submit
                    </button>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {

    return {};
}

export default connect(mapStateToProps, { subscribeEmail })(EmailCollection);


