import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getArtistInfo } from '../../actions'

import SocialLinks from './SocialLinks';
import ArtPieces from './ArtPieces';

class ArtistProfile extends Component {

    constructor(props) {
        super(props);
        this.state = {
            artistInfo: {}
        }
    }

    componentWillMount() {
        this.props.getArtistInfo(this.props.params.id)
    }

    render() {
        if(this.props.artistInfo) {
            // console.log(this.props.artistInfo);
            let aI = this.props.artistInfo;
            let aP = aI.artistProfile;
            return (
                <div className="container" id="pgArtistProfile">

                    {/* FIRST ROW || Names, Social, */}
                    <div className="row row1">
                        <div className="profileImg col-sm-4">
                            <img src={aP.profileImgUrl} alt=""/>
                        </div>
                        <div className="col-sm-8">
                            <p className="publicName">{aP.publicName}</p> 
                            <p className="names">{aP.fname} {aP.lname}</p> 
                            <SocialLinks linksObj={aI.socialMediaLinks} />
                        </div>
                    </div>
                    <hr/>

                    {/* SECOND ROW */}
                    <div className="row row2">
                        <div className="col-sm-12">
                            {/* <h3>{aP.fname}'s Pieces</h3> */}

                            <ArtPieces pieces={aI.artPieces} artistUsername={this.props.params.id}/> 
                        </div>
                    </div>
                </div>
            )
        } else {
            return (
                <div id="pgArtistProfile">
                    <div className="lds-roller"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
                </div>
            )
        }

    }
}

const mapStateToProps = state => {
    // console.log(state)

    let artistInfo = false;

    if(state.artistInfo) {
        artistInfo = state.artistInfo;
    }

    return { artistInfo }
}


export default connect(mapStateToProps, { getArtistInfo })(ArtistProfile);


