import React, { Component } from 'react';
import { Link } from 'react-router';

import PieceComponent from './PieceComponent';

class PieceRow extends Component {

    render() {
        let piecesRowArr = this.props.piecesRowArr;
        // console.log(piecesRowArr)

        let artistUsername = this.props.artistUsername;

        if(piecesRowArr.length > 0) {
            return (
                <div className="row">
                    {
                        piecesRowArr.map((pieceObj, index) => 
                            <Link to={'/artist/' + artistUsername + '/' + pieceObj.imgId} key={index} >
                                <PieceComponent pieceObj={pieceObj} />
                            </Link>
                        )
                    }
                </div>
            )
        } else {
            return (
                <div>
                </div>
            )
        }

    }
}


export default PieceRow;


