import React, { Component } from 'react';
import { connect } from 'react-redux';
import { artistEmailSignup } from '../../actions';
import { _amp } from '../../index';

class ArtistSignup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            artistEmail: '',
            artistPorfolio: ''
        }
    }

    submitArtistSignup() {
        if(this.validateEmail(this.state.artistEmail)) {
            _amp.logEvent('Artist email signup', {artistEmail: this.state.artistEmail});
            this.props.artistEmailSignup(this.state.artistEmail);
            alert('Great! We\'ll contact you shortly to get you setup!.')
            this.setState({ artistEmail: '' });
        } else {
            alert('Not a valid email address.');
        }
    }

    validateEmail(email) {
        // eslint-disable-next-line
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }

    render() {
        return (
            <div className="container" id="artistSignup">
                <div className="col-sm-8 col-sm-offset-2 text-center">
                    <p className="line1">Are you an artist?</p>
                    <p className="line2">Signup to be featured on <span className="logoText">muralist</span></p>
                    <input
                        type="text"
                        placeholder="Email"
                        className="form-control"
                        value={this.state.artistEmail}
                        onChange={event => this.setState({ artistEmail: event.target.value })}
                    />

                    <button
                        className="btn"
                        type="button"
                        onClick={() => this.submitArtistSignup()}
                    >
                        Submit
                    </button>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {

    return {};
}

export default connect(mapStateToProps, { artistEmailSignup } )(ArtistSignup);


