import React, { Component } from 'react';
import { Link } from 'react-router';

class ArtistProfileSmall extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        let { artistProfile, artPieces } = this.props.artistObj;
        let artistUsername = artistProfile.artistUsername;
        let ftArtPiece = Object.keys(artPieces)[0];
        let imgArr = artPieces[ftArtPiece]['imgUrls'];
        let imgKey = Object.keys(imgArr)[0];
        let imgUrl = imgArr[imgKey];

        if(this.props.artistObj.showInApp !== undefined && this.props.artistObj.showInApp === false) {
            return (
                <span></span>
            )
        } else {
            return (
                <div className="artistBlock">
                    <Link to={'/artist/' + artistUsername}> 
                        <div className="text-center col-sm-3">
                            <img src={imgUrl} className="featuredImg" alt=""/>
                        </div>
                        <div className="rightCol col-sm-9 text-center">
                            <p className="publicName">{artistProfile.publicName}</p>
                            <p className="fname">Name: {artistProfile.fname}</p>
                            <p className="ig">IG: {artistProfile.igUsername}</p>
                        </div>
                    </Link>
                </div>
            )
        }
    }
}


export default ArtistProfileSmall;


